.program {
  $this: &;
  &__tabs {
    @extend .grid;
    margin-bottom: 0; }
  &__tab-wrap {
    @extend ._col-6;
    display: block;
    margin-bottom: 0; }
  &__tab {
    display: block;
    width: 100%;
    box-shadow: 0px 0px 38px 0px rgba(0, 0, 0, 0.09);
    background-color: $white;
    text-align: center;
    text-transform: uppercase;
    color: $accent;
    @include font-title;
    @include css-lock(21, 36, 320, 1399, font-size);
    padding: .2em .5em;
    @include tr(color .3s, background-color .3s);
    @include hover {
      @extend #{$this}__tab--active; }
    &--active {
      background-color: $accent;
      color: $white; } }
  &__content {
    background-color: $white;
    padding: 25px 90px;
    box-shadow: 0px 0px 38px 0px rgba(0, 0, 0, 0.09);
    @include xl {
      padding: 25px; }
    @include sm {
      padding: 15px; } }
  &__item {
    @include hidden;
    max-height: 0;
    overflow: hidden;
    @include tr(opacity .3s, visibility .3s);
    &--active {
      @include visible;
      max-height: none;
      overflow: visible; } } }

.scedule {
  &__item {
    @include notlast {
      margin-bottom: 25px;
      @include md {
        margin-bottom: 15px; } } } }

.scedule-item {
  @include flex;
  @include sm {
    display: block; }
  &__time {
    text-transform: uppercase;
    color: $accent;
    text-align: right;
    font-size: em(30);
    @include font-title;
    width: percentage(130/1230);
    @include xl {
      width: 12%; }
    @include lg {
      width: 15%; }
    @include md {
      width: 18%; }
    @include sm {
      width: auto;
      text-align: left; } }
  &__content {
    width: percentage((1230 - 130) / 1230);
    padding-left: 30px;
    padding-top: .5em;
    @include xl {
      width: 88%; }
    @include lg {
      width: 85%;
      padding-left: 15px; }
    @include md {
      width: 82%; }
    @include sm {
      width: auto;
      padding-left: 0; } }
  &__title {
    font-weight: 700;
    margin-bottom: .2em; }
  &__text {
    blockquote {
      font-style: italic;
      background-color: $white;
      box-shadow: 0px 0px 14px 0px rgba(0, 0, 0, 0.1);
      padding: 1em;
      color: $accent;
      margin-top: 35px;
      @include md {
        margin-top: 20px; } }
    button, a:not(.scedule-item__sign-link) {
      color: $blue;
      text-decoration: underline; } }
  &__sign {
    strong {
      font-size: $fz-sm;
      font-style: italic; } }
  &__sign-link {
    display: inline-flex;
    align-items: center;
    color: $blue;
    .icon {
      fill: #e2574c;
      margin-left: .2em; }
    span {
      text-decoration: underline; } } }


